import { newE2EPage } from '@stencil/core/testing';

describe('presto-playlist', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-playlist></presto-playlist>');

    const element = await page.find('presto-playlist');
    expect(element).toHaveClass('hydrated');
  });
});
